<template>
  <a-popover>
    <template #content>
      <div class="popover-description">
        <p>   {{ tickets.length > 1 ? '  ,   ' : '  ,   ' }}   .</p>
        <p>        ,   {{ tickets.length > 1 ? '' : '' }}   </p>
      </div>
    </template>
    <my-timer v-if="!isTimeExpired" :duration="900" :callback="setExpiredCallback" class="checkout-timer"/>
  </a-popover>
  <div v-if="isTimeExpired === false" class="checkout-step-content">
    <div class="step-header-wrap">
      <h1 class="step-header">{{ tickets.length > 1 ? '   !' : '   !' }}    ..</h1>
      <h2 class="step-header-description">       .</h2>
    </div>
    <div class="pay-wrap">
      <div>
        <div class="pay-left-col">
          <h1 class="pay-left-col__film-name">{{film.nameRu}}</h1>
          <div class="pay-left-col__tags-container">
            <my-tag :text="ageLimits" type="dotted"/>
            <my-tag :text="session.format"
                    type="green"
                    :is-animated="session.format === '3D'"
                    :popover="true">
              {{session.format === '3D' ? '3D-      ' : null}}
            </my-tag>
          </div>
          <chosen-seats-info type="full"></chosen-seats-info>
        </div>
      </div>
      <div class="pay-right-col">
      </div>
    </div>
    </div>
  <div v-else class="header-expired-wrap">
    <h1 class="step-header">,    .</h1>
    <h3 class="step-description">     </h3>
  </div>
</template>
<script>
import MyTimer from './UI/MyTimer';
import { mapGetters, mapMutations, mapState } from 'vuex';
import MyTag from './UI/MyTag';
import ChosenSeatsInfo from './ChosenSeatsInfo';

export default {
  name: 'CheckoutStep',
  components: { MyTimer, MyTag, ChosenSeatsInfo},
  data() {
    return {};
  },
  methods: {
    ...mapMutations({
      setIsTimeExpired: 'setIsTimeExpired',
      clearTickets: 'clearTickets'
    }),
    setExpiredCallback() {
      this.setIsTimeExpired(true);
      this.clearTickets();
    }
  },
  computed: {
    ...mapState({
      isTimeExpired: state => state.isTimeExpired,
      tickets: state => state.selectedTickets,
      session: state => state.selectedSession,
      film: state => state.selectedFilm,
      ageLimits: state => state.ageLimitsOfSelectedFilm
    }),
    ...mapGetters({
      getCountInfoAboutTickets: 'getCountInfoAboutTickets'
    })
  },
  unmounted() {
    this.setIsTimeExpired(false);
  }
};
</script>

<style scoped>
.checkout-timer {
  width: 100px;
  margin-bottom: 10px;
}

.step-header-wrap {
  padding-bottom: 20px;
  border-bottom: 1px solid var(--light-grey);
}

.step-header {
  font-size: 23px;
  margin-bottom: 10px;
  line-height: 1.2;
}

.step-header-description {
  font-size: 17px;
  font-weight: 400;
}

.pay-wrap {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
}

.pay-left-col__film-name {
  font-size: 27px;
  margin-bottom: 7px;
}

.pay-left-col__tags-container {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 10px;
}

.header-expired-wrap {
  text-align: center;
}

@media (max-width: 460px) {
  .step-header {
    font-size: 20px;
  }
}
</style>